/**
 * @name uiBbAccountCard
 * @type {object}
 *
 * @property {string} accountName Account name
 * @property {string} accountImage Image url, shown in avatar field (if it is enabled)
 * @property {string} accountNumber Account number
 * @property {number} amount Account's balance
 * @property {string} currency Currency's ISO 4217 code
 * @property {AdditionalInfo[]} additionalInfo Array with additional card info objects
 * @property {boolean} showAvatar Display avatar field with image or name initials
 */

/**
 * @typedef {Object} AdditionalInfo
 * @property {?string} name Additional info label
 * @property {?number} amount Additional info row's amount
 * @property {?string} currency Currency's ISO 4217 code
 */

const uiBbAccountCard = {
  bindings: {
    accountName: '<',
    accountImage: '<',
    accountNumber: '<',
    amount: '<',
    currency: '<',
    additionalInfo: '<',
    showAvatar: '<',
  },
  template: `
    <div class="card">
      <div class="card-body d-inline-flex flex-wrap justify-content-between">

        <div class="d-inline-flex justify-content-between d-flex flex-wrap 
          align-items-center flex-basis pb-2">

          <div
            data-ng-if="$ctrl.showAvatar">
            <ui-bb-avatar-ng
              class="bb-avatar media rounded-circle img-thumbnail 
              text-center mr-3"
              data-name="$ctrl.accountName"
              data-image="$ctrl.accountImage">
            </ui-bb-avatar-ng>
          </div>

          <div class="d-flex flex-column">
            <span class="font-weight-bold">{{ $ctrl.accountName }}</span>
            <span>{{ $ctrl.accountNumber }}</span>
          </div>

          <span class="text-right card-account-balance">
            <ui-bb-format-amount-ng
              data-amount="$ctrl.amount"
              data-currency="$ctrl.currency"
              data-wrap
            ></ui-bb-format-amount-ng>
          </span>

        </div>

        <div class="flex-basis">
          <div class="d-flex flex-column">
            <div class="d-flex justify-content-between text-left text-muted"
              data-ng-repeat="additional in $ctrl.additionalInfo track by additional.name"
              >
              <small class="text-left">{{ additional.name }}</small>
              <small>
                <ui-bb-format-amount-ng
                  class="text-right"
                  data-amount="additional.amount"
                  data-currency="additional.currency"
                  data-wrap
                ></ui-bb-format-amount-ng>
              </small>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
};

export default uiBbAccountCard;



